
"use client";

import type { Project } from '@/types/project';
import React, { useRef, useEffect, useState } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { CSS2DRenderer, CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
import { useThemeContext } from '@/contexts/ThemeContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import NextImage from 'next/image';
import { Eye, Bot } from 'lucide-react'; 
import ImagePreviewModal from '@/components/portfolio/ImagePreviewModal';
import ChatWidget from '@/components/chat/ChatWidget'; 
import { useTranslations } from '@/hooks/useTranslations';


interface ThreeCanvasProps {
  projects: Project[];
}

const ThreeCanvas: React.FC<ThreeCanvasProps> = ({ projects }) => {
  const mountRef = useRef<HTMLDivElement>(null);
  const { theme } = useThemeContext();
  const t = useTranslations();
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isPreviewModalOpen, setIsPreviewModalOpen] = useState(false);
  const [isChatOpenWithContext, setIsChatOpenWithContext] = useState(false);
  const [chatInitialMessage, setChatInitialMessage] = useState<string | undefined>();
  const [chatProjectContext, setChatProjectContext] = useState<string | undefined>();
  
  const projectMeshesRef = useRef<THREE.Mesh[]>([]);


  useEffect(() => {
    if (!mountRef.current) return;
    if (typeof window === 'undefined') return;

    setIsLoading(true);
    const currentMount = mountRef.current;

    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, currentMount.clientWidth / currentMount.clientHeight, 0.1, 1000);
    camera.position.set(0, 5, 20); 

    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setSize(currentMount.clientWidth, currentMount.clientHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    currentMount.appendChild(renderer.domElement);

    const labelRenderer = new CSS2DRenderer();
    labelRenderer.setSize(currentMount.clientWidth, currentMount.clientHeight);
    labelRenderer.domElement.style.position = 'absolute';
    labelRenderer.domElement.style.top = '0px';
    labelRenderer.domElement.style.pointerEvents = 'none';
    currentMount.appendChild(labelRenderer.domElement);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true;
    controls.minDistance = 5;
    controls.maxDistance = 50;
    
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambientLight);
    const directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
    directionalLight.position.set(5, 10, 7.5); 
    scene.add(directionalLight);

    const backgroundGeometry = new THREE.SphereGeometry(500, 60, 40);
    backgroundGeometry.scale(-1, 1, 1);

    // Theme-aware colors from CSS variables
    const computedRootStyle = getComputedStyle(document.documentElement);
    const getHSLColor = (cssVar: string) => {
      const hslString = computedRootStyle.getPropertyValue(cssVar).trim();
      const [h, s, l] = hslString.split(' ').map(parseFloat);
      const color = new THREE.Color();
      color.setHSL(h / 360, s / 100, l / 100);
      return color;
    };
    
    const backgroundShaderMaterial = new THREE.ShaderMaterial({
      uniforms: {
        uTime: { value: 0 },
        uTheme: { value: theme === 'dark' ? 0.0 : 1.0 }, 
        // Vizioon (Dark) Theme Colors (from globals.css :root --background, --primary, --accent related)
        uColor1Dark: { value: new THREE.Color(0x242A3B) }, // --background (Dark Navy Blue)
        uColor2Dark: { value: new THREE.Color(0xBE63F2) }, // --primary (Electric Purple)
        uColor3Dark: { value: new THREE.Color(0x63F2E2) }, // --accent (Cyan)
        // Light Theme Colors (from globals.css html.light --background, --primary, --accent related)
        uColor1Light: { value: new THREE.Color(0xE0F2FE) }, // Light --background
        uColor2Light: { value: new THREE.Color(0x7CB9E8) }, // Light --primary (example: light blue)
        uColor3Light: { value: new THREE.Color(0xADD8E6) }, // Light --accent (example: lighter blue)
      },
      vertexShader: `
        varying vec2 vUv;
        void main() {
          vUv = uv;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
      `,
      fragmentShader: `
        uniform float uTime;
        uniform float uTheme; 
        uniform vec3 uColor1Dark; 
        uniform vec3 uColor2Dark; 
        uniform vec3 uColor3Dark;
        uniform vec3 uColor1Light; 
        uniform vec3 uColor2Light; 
        uniform vec3 uColor3Light;
        varying vec2 vUv;

        float random(vec2 st) {
            return fract(sin(dot(st.xy, vec2(12.9898,78.233))) * 43758.5453123);
        }

        float noise(vec2 st) {
            vec2 i = floor(st);
            vec2 f = fract(st);
            float a = random(i);
            float b = random(i + vec2(1.0, 0.0));
            float c = random(i + vec2(0.0, 1.0));
            float d = random(i + vec2(1.0, 1.0));
            vec2 u = f * f * (3.0 - 2.0 * f); 
            return mix(a, b, u.x) + (c - a) * u.y * (1.0 - u.x) + (d - b) * u.y * u.x;
        }

        float fbm(vec2 st) {
            float value = 0.0;
            float amplitude = 0.5;
            // float frequency = 0.0; // Original, but frequency is not used in the loop below
            for (int i = 0; i < 4; i++) { 
                value += amplitude * noise(st);
                st *= 2.0; // This increases frequency
                amplitude *= 0.5;
            }
            return value;
        }

        void main() {
            vec2 uv = vUv;
            float time = uTime * 0.03;

            // Mix colors based on theme
            vec3 color1 = mix(uColor1Dark, uColor1Light, uTheme);
            vec3 color2 = mix(uColor2Dark, uColor2Light, uTheme);
            vec3 color3 = mix(uColor3Dark, uColor3Light, uTheme);
            
            vec3 baseColor = color1 * 0.8; // Make base color slightly darker for depth

            // Nebula effect
            float nebulaPattern = fbm(uv * 3.0 + time * 0.1 + vec2(sin(time*0.05), cos(time*0.08)));
            vec3 nebulaColoration = mix(color2, color3, sin(uv.x * 6.0 + time * 0.3 + nebulaPattern * 2.0));
            float nebulaStrength = smoothstep(0.4, 0.7, nebulaPattern) * (uTheme > 0.5 ? 0.3 : 0.5); // Softer nebula for light theme
            
            // Stars
            float starDensity = uTheme > 0.5 ? 60.0 : 150.0;
            float starValue = random(floor(uv * starDensity + sin(time * 0.05 + uv.y * 15.0))); 
            starValue = pow(starValue, uTheme > 0.5 ? 15.0 : 25.0); 
            vec3 starColorEffect = vec3(starValue * (uTheme > 0.5 ? 0.5 : 0.7)); // Less bright stars for light theme
            
            vec3 finalColor = baseColor + nebulaColoration * nebulaStrength + starColorEffect;
            
            // Vignette
            float dist = distance(uv, vec2(0.5));
            finalColor *= smoothstep(0.8, 0.2, dist);


            gl_FragColor = vec4(clamp(finalColor, 0.0, 1.0), 1.0);
        }
      `,
      side: THREE.BackSide,
    });
    const backgroundMesh = new THREE.Mesh(backgroundGeometry, backgroundShaderMaterial);
    scene.add(backgroundMesh);

    const textureLoader = new THREE.TextureLoader();
    textureLoader.crossOrigin = 'anonymous';
    
    let texturesProcessedCount = 0;
    const totalTextures = projects.length;
    projectMeshesRef.current = []; 

    if (totalTextures === 0) {
      setIsLoading(false);
    } else {
      // Logarithmic spiral parameters
      const a = 1; // Scale factor for the spiral
      const b = 0.3; // Controls how tightly the spiral winds
      const numTurns = 2; // Number of full turns the spiral makes
      const verticalSpread = 3; // How much Y variation

      projects.forEach((project, index) => {
        const geometry = new THREE.BoxGeometry(2, 2, 2); // Simple cube for now
        
        const texture = textureLoader.load(
          project.imageUrl,
          () => { // onLoad
            texturesProcessedCount++;
            if (texturesProcessedCount === totalTextures) setIsLoading(false);
          },
          undefined, // onProgress - not typically used for single texture loads
          (err) => { // onError
            console.error(`Failed to load texture for ${project.title}:`, err);
            texturesProcessedCount++; // Still count it as processed to unblock loading state
            if (texturesProcessedCount === totalTextures) setIsLoading(false);
          }
        );
        const material = new THREE.MeshStandardMaterial({ map: texture, color: 0xffffff });
        const mesh = new THREE.Mesh(geometry, material);

        // Calculate position on a logarithmic spiral in the XZ plane
        // We use (projects.length - 1) or 1 to avoid division by zero if only one project
        const angle = (index / (projects.length > 1 ? projects.length -1 : 1)) * numTurns * Math.PI * 2; 
        const radius = a * Math.exp(b * angle);
        
        mesh.position.set(
          Math.cos(angle) * radius,
          Math.sin(angle * 1.5 + index * 0.5) * verticalSpread - (verticalSpread/2), // Y oscillation along spiral
          Math.sin(angle) * radius
        );
        mesh.lookAt(new THREE.Vector3(0, mesh.position.y, 0)); // Look towards the Y axis at their height

        mesh.userData = { id: project.id, type: 'project' };
        projectMeshesRef.current.push(mesh);
        scene.add(mesh);

        // Create and add CSS2D label
        const labelDiv = document.createElement('div');
        labelDiv.className = 'project-label'; // Used for styling via globals.css
        labelDiv.textContent = project.title;
        // Initial styles, will be updated dynamically based on theme in animate loop
        labelDiv.style.padding = '4px 8px';
        labelDiv.style.borderRadius = '4px';
        labelDiv.style.fontSize = '14px';
        labelDiv.style.fontFamily = 'var(--font-geist-sans)';
        
        const label = new CSS2DObject(labelDiv);
        label.position.set(0, 1.5, 0); // Position label above the mesh
        mesh.add(label);
      });
    }

    const raycaster = new THREE.Raycaster();
    const mouse = new THREE.Vector2();

    const onClick = (event: MouseEvent) => {
      if (!currentMount) return;
      // Adjust mouse click to be relative to the canvas
      const rect = renderer.domElement.getBoundingClientRect();
      mouse.x = ((event.clientX - rect.left) / rect.width) * 2 - 1;
      mouse.y = -((event.clientY - rect.top) / rect.height) * 2 + 1;
      raycaster.setFromCamera(mouse, camera);
      const intersects = raycaster.intersectObjects(projectMeshesRef.current);

      if (intersects.length > 0) {
        const clickedObject = intersects[0].object;
        if (clickedObject.userData.type === 'project') {
          const foundProject = projects.find(p => p.id === clickedObject.userData.id);
          setSelectedProject(foundProject || null);
          setIsChatOpenWithContext(false); // Close chat if a new project is selected
        }
      }
    };
    renderer.domElement.addEventListener('click', onClick);

    const clock = new THREE.Clock();
    const animate = () => {
      requestAnimationFrame(animate);
      controls.update();
      backgroundShaderMaterial.uniforms.uTime.value = clock.getElapsedTime();
      backgroundShaderMaterial.uniforms.uTheme.value = theme === 'dark' ? 0.0 : 1.0;
      
      // Dynamically update label styles based on CSS variables
      scene.traverse((object) => {
        if (object instanceof CSS2DObject && object.element.classList.contains('project-label')) {
          // Ensure document and document.documentElement are available
          if (typeof document !== 'undefined' && document.documentElement) {
            const styles = getComputedStyle(document.documentElement);
            const fgColor = styles.getPropertyValue('--foreground').trim();
            const cardColor = styles.getPropertyValue('--card').trim(); // Or --background for less opacity

            object.element.style.color = `hsl(${fgColor})`;
            object.element.style.backgroundColor = `hsla(${cardColor}, 0.7)`; // Semi-transparent card color
            object.element.style.border = `1px solid hsla(${styles.getPropertyValue('--border').trim()}, 0.5)`;
          }
        }
      });

      renderer.render(scene, camera);
      labelRenderer.render(scene, camera);
    };
    animate();

    const handleResize = () => {
      if (!currentMount) return;
      camera.aspect = currentMount.clientWidth / currentMount.clientHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(currentMount.clientWidth, currentMount.clientHeight);
      labelRenderer.setSize(currentMount.clientWidth, currentMount.clientHeight);
    };
    window.addEventListener('resize', handleResize);
    
    // Cleanup on unmount
    return () => {
      window.removeEventListener('resize', handleResize);
      renderer.domElement.removeEventListener('click', onClick);
      // Check if renderer.domElement is still a child of currentMount before removing
      if (currentMount && renderer.domElement.parentNode === currentMount) {
         currentMount.removeChild(renderer.domElement);
      }
      if (currentMount && labelRenderer.domElement.parentNode === currentMount) {
         currentMount.removeChild(labelRenderer.domElement);
      }
      renderer.dispose();

      // Dispose geometries, materials, textures
      scene.traverse(object => {
        if (object instanceof THREE.Mesh) {
          if (object.geometry) object.geometry.dispose();
          if (object.material) {
            // Check if material is an array (multi-material) or single
            const materials = Array.isArray(object.material) ? object.material : [object.material];
            materials.forEach(mat => {
              if (mat.map) mat.map.dispose(); // Dispose texture
              mat.dispose(); // Dispose material
            });
          }
        }
        // Remove CSS2DObjects' elements from DOM to prevent memory leaks
        if (object instanceof CSS2DObject) {
            if (object.element && object.element.parentNode) {
                object.element.parentNode.removeChild(object.element);
            }
        }
      });
      backgroundGeometry.dispose();
      backgroundShaderMaterial.dispose();
      projectMeshesRef.current = [];
    };
  }, [theme, projects]); // Removed 't' as it's stable from useTranslations

  // Effect to manage body cursor style based on modal/chat states
  useEffect(() => {
    if (isPreviewModalOpen || selectedProject || isChatOpenWithContext) {
      document.body.classList.add('cursor-default-override');
    } else {
      document.body.classList.remove('cursor-default-override');
    }
    // Cleanup function to remove the class when the component unmounts or dependencies change
    return () => {
      document.body.classList.remove('cursor-default-override');
    };
  }, [isPreviewModalOpen, selectedProject, isChatOpenWithContext]);

  const handleOpenPreview = () => {
    if (selectedProject) {
      setIsPreviewModalOpen(true);
    }
  };

  const handleAskVizioon = () => {
    if (selectedProject) {
      setChatInitialMessage(t.threeCanvas.chatInitialMessage.replace('{projectTitle}', selectedProject.title));
      setChatProjectContext(selectedProject.chatContext || selectedProject.description);
      setIsChatOpenWithContext(true); 
      setSelectedProject(null); // Close the project card once chat is initiated
    }
  };

  return (
    <div className="flex-grow relative w-full h-full">
      {isLoading && (
        <div className="absolute inset-0 flex items-center justify-center bg-background/80 z-10">
          {/* You can add a spinner or a more styled loading message here */}
          <p className="text-xl text-foreground">{t.threeCanvas.loading}</p>
        </div>
      )}
      <div ref={mountRef} className="w-full h-full" />
      {selectedProject && !isPreviewModalOpen && !isChatOpenWithContext && (
        // This div controls the position of the card on the screen
        <div className="absolute top-4 right-4 z-20 max-w-sm w-full p-2">
          <Card className="tilt-card shadow-2xl bg-card/90 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-primary">{selectedProject.title}</CardTitle>
              <div className="relative mt-2">
                <NextImage 
                  src={selectedProject.imageUrl} 
                  alt={selectedProject.title}
                  width={400} 
                  height={300} 
                  className="rounded-md object-cover w-full h-48" // Fixed height for image
                  data-ai-hint={selectedProject.dataAiHint}
                />
                <Button 
                  variant="ghost" 
                  size="icon" 
                  className="absolute top-2 right-2 bg-black/30 hover:bg-black/50 text-white hover:text-primary rounded-full"
                  onClick={handleOpenPreview}
                  aria-label={t.threeCanvas.viewImageLabel}
                  title={t.threeCanvas.viewImageLabel}
                >
                  <Eye className="h-5 w-5" />
                </Button>
              </div>
            </CardHeader>
            <CardContent>
              <CardDescription className="text-card-foreground/80 mb-4 h-20 overflow-y-auto text-sm">
                {selectedProject.description}
              </CardDescription>
              <div className="flex gap-2 mt-2">
                {selectedProject.link && (
                  <Button asChild variant="default" className="bg-primary hover:bg-primary/90 text-primary-foreground flex-grow">
                    <a href={selectedProject.link} target="_blank" rel="noopener noreferrer">
                      {t.threeCanvas.viewProjectButton}
                    </a>
                  </Button>
                )}
                <Button 
                  variant="outline" 
                  onClick={handleAskVizioon}
                  className="flex-grow"
                  aria-label={t.threeCanvas.askVizioonLabel.replace('{projectTitle}', selectedProject.title)}
                  title={t.threeCanvas.askVizioonTooltip}
                >
                  <Bot className="mr-2 h-4 w-4" /> {t.threeCanvas.askButton}
                </Button>
              </div>
              <Button variant="outline" onClick={() => setSelectedProject(null)} className="mt-2 w-full">
                {t.threeCanvas.closeButton}
              </Button>
            </CardContent>
          </Card>
        </div>
      )}
      {selectedProject && isPreviewModalOpen && (
        <ImagePreviewModal
          imageUrl={selectedProject.imageUrl}
          altText={selectedProject.title}
          isOpen={isPreviewModalOpen}
          onClose={() => {
            setIsPreviewModalOpen(false);
            // Keep selectedProject card visible when modal closes, instead of setting selectedProject to null
          }}
        />
      )}
      {/* Conditionally render ChatWidget only when isChatOpenWithContext is true to force re-mount with new props */}
      {isChatOpenWithContext && (
        <ChatWidget 
          key={Date.now()} // Force re-mount with a unique key
          startOpen={true} 
          initialMessage={chatInitialMessage}
          projectContext={chatProjectContext}
        />
      )}
    </div>
  );
};

export default ThreeCanvas;

